@if $fadeInScaleBig == true {
	@keyframes fadeInScaleBig {
		0% {
			transform: scale(2);
			opacity: 0;
		}
		100% {
			transform: scale(1);
			opacity: 1;
		}
	}
}

@mixin fadeInScaleBig($duration, $delay, $function, $fill, $visibility) {
	animation-name: fadeInScaleBig;
   	@include duration($duration);
	@include delay($delay);
	@include function($function);
	@include fill-mode($fill);
	@include visibility($visibility);
}
