@if $fadeOut == true {
	@keyframes fadeOut {
		0% {opacity: 1;}
		100% {opacity: 0;}
	}
}

@mixin fadeOut($duration, $delay, $function, $fill, $visibility) {
	animation-name: fadeOut;
	@include duration($duration);
	@include delay($delay);
	@include function($function);
	@include fill-mode($fill);
	@include visibility($visibility);
}
