@if $fadeInScale == true {
	@keyframes fadeInScale {
		0% {
			transform: scale(.7);
			opacity: 0;
		}
		100% {
			transform: scale(1);
			opacity: 1;
		}
	}
}

@mixin fadeInScale($duration, $delay, $function, $fill, $visibility) {
	animation-name: fadeInScale;
   	@include duration($duration);
	@include delay($delay);
	@include function($function);
	@include fill-mode($fill);
	@include visibility($visibility);
}
